import { reactive } from "vue"
import axios from "./axios"

const auth = reactive({
  isLoggedIn: false,
  isStaff: false,
  isInitialized: false,
  isInitializing: false, // Add flag to track initialization process

  init() {
    if (this.isInitializing) {
      return // Prevent multiple simultaneous initializations
    }

    this.isInitializing = true
    const isDemoMode = window.location.hostname.includes("github.io") || process.env.VUE_APP_DEMO_MODE === "true"

    try {
      if (isDemoMode) {
        // In demo mode, check localStorage for auth state
        this.isLoggedIn = !!localStorage.getItem("access")
        this.isStaff = localStorage.getItem("is_staff") === "true"
      } else {
        // In development mode
        this.isLoggedIn = !!localStorage.getItem("access")
        this.isStaff = localStorage.getItem("is_staff") === "true"
      }

      this.isInitialized = true
      console.log(" Auth initialized:", { isLoggedIn: this.isLoggedIn, isStaff: this.isStaff })
    } catch (error) {
      console.error("Auth initialization error:", error)
      this.isLoggedIn = false
      this.isStaff = false
      this.isInitialized = true
    } finally {
      this.isInitializing = false
    }
  },

  async checkTokenValidity() {
    // Skip check in demo mode
    if (process.env.VUE_APP_DEMO_MODE === "true") {
      this.isInitialized = true
      return true
    }

    const token = localStorage.getItem("access")
    if (!token) {
      this.isInitialized = true
      return false
    }

    try {
      await axios.get("user/")
      this.isInitialized = true
      return true
    } catch (error) {
      console.log("Token invalid or expired, logging out")
      this.logout()
      return false
    }
  },

  logout(redirectToGithubPages = false) {
    console.log(" Logging out...")

    // Preserve cart data
    const cart = localStorage.getItem("cart")

    // Clear all auth-related data
    localStorage.removeItem("access")
    localStorage.removeItem("refresh")
    localStorage.removeItem("is_staff")
    localStorage.removeItem("user_id")
    localStorage.removeItem("username")

    // Restore cart if it existed
    if (cart) {
      localStorage.setItem("cart", cart)
    }

    // Reset auth state
    this.isLoggedIn = false
    this.isStaff = false
    this.isInitialized = false
    this.isInitializing = false

    console.log(" Auth state reset, reinitializing...")

    // Force reinitialization after a short delay
    setTimeout(() => {
      this.init()
    }, 100)

    if (redirectToGithubPages) {
      // This will be handled by the component using router
    }
  },

  // Enhanced method to wait for initialization with timeout
  async waitForInitialization(timeout = 5000) {
    return new Promise((resolve, reject) => {
      if (this.isInitialized) {
        resolve()
        return
      }

      const startTime = Date.now()
      const checkInterval = setInterval(() => {
        if (this.isInitialized) {
          clearInterval(checkInterval)
          resolve()
        } else if (Date.now() - startTime > timeout) {
          clearInterval(checkInterval)
          console.warn(" Auth initialization timeout")
          reject(new Error("Auth initialization timeout"))
        }
      }, 50)
    })
  },

  // Force reinitialize with proper cleanup
  async reinitialize() {
    console.log(" Force reinitializing auth...")
    this.isInitialized = false
    this.isInitializing = false

    // Wait a bit to ensure cleanup
    await new Promise((resolve) => setTimeout(resolve, 100))

    this.init()

    // Wait for initialization to complete
    try {
      await this.waitForInitialization()
      console.log(" Auth reinitialization complete")
    } catch (error) {
      console.error(" Auth reinitialization failed:", error)
    }
  },

  // Method to set auth state after successful login
  setAuthState(isLoggedIn, isStaff = false) {
    console.log(" Setting auth state:", { isLoggedIn, isStaff })
    this.isLoggedIn = isLoggedIn
    this.isStaff = isStaff
    this.isInitialized = true
    this.isInitializing = false
  },
})

// Initialize immediately
auth.init()

export { auth }
