<template>
  <div class="login-container">
    <div class="login-card">
      <div class="login-header">
        <div class="logo-container">
          <i class="bi bi-bag-check-fill"></i>
        </div>
        <h2>Welcome Back</h2>
        <p>Sign in to your account to continue</p>
      </div>

      <form @submit.prevent="handleLogin" class="login-form">
        <div class="form-group">
          <label for="username">Username</label>
          <div class="input-group">
            <i class="bi bi-person input-icon"></i>
            <input
              id="username"
              v-model="username"
              type="text"
              placeholder="Enter your username"
              required
              :disabled="isLoading"
            />
          </div>
        </div>

        <div class="form-group">
          <label for="password">Password</label>
          <div class="input-group">
            <i class="bi bi-lock input-icon"></i>
            <input
              id="password"
              v-model="password"
              :type="showPassword ? 'text' : 'password'"
              placeholder="Enter your password"
              required
              :disabled="isLoading"
            />
            <button
              type="button"
              class="password-toggle"
              @click="showPassword = !showPassword"
              :disabled="isLoading"
            >
              <i :class="showPassword ? 'bi bi-eye-slash' : 'bi bi-eye'"></i>
            </button>
          </div>
        </div>

        <div class="form-options">
          <label class="checkbox-container">
            <input type="checkbox" v-model="rememberMe" :disabled="isLoading">
            <span class="checkmark"></span>
            Remember me
          </label>
        </div>

        <button type="submit" class="login-btn" :disabled="isLoading">
          <span v-if="isLoading" class="spinner"></span>
          <i v-else class="bi bi-box-arrow-in-right"></i>
          {{ isLoading ? 'Signing in...' : 'Sign In' }}
        </button>

        <div v-if="error" class="error-message">
          <i class="bi bi-exclamation-triangle"></i>
          {{ error }}
        </div>
      </form>

      <div class="login-footer">
        <p>Don't have an account? <router-link to="/register">Create Account</router-link></p>
      </div>

      <!-- Demo Credentials Info -->
      <div class="demo-info">
        <h4>Demo Credentials</h4>
        <div class="demo-accounts">
          <div class="demo-account">
            <strong>Admin:</strong> admin / admin
          </div>
          <div class="demo-account">
            <strong>User:</strong> user / user
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { ref } from 'vue'
import { useRouter } from 'vue-router'
import axios from '../axios'
import { auth } from '../auth'
import { useToastStore } from '../stores/toastStore'

export default {
  name: 'UserLogin',
  setup() {
    const router = useRouter()
    const { success, error: showError } = useToastStore()
    
    const username = ref('')
    const password = ref('')
    const showPassword = ref(false)
    const rememberMe = ref(false)
    const isLoading = ref(false)
    const error = ref('')

    const handleLogin = async () => {
      if (isLoading.value) return
      
      error.value = ''
      isLoading.value = true

      try {
        console.log(' Attempting login for:', username.value)
        
        const response = await axios.post('token/', {
          username: username.value,
          password: password.value
        })

        const { access, refresh } = response.data
        
        // Store tokens
        localStorage.setItem('access', access)
        localStorage.setItem('refresh', refresh)
        localStorage.setItem('username', username.value)

        // Determine user type and set staff status
        const isStaff = username.value === 'admin'
        localStorage.setItem('is_staff', isStaff.toString())
        localStorage.setItem('user_id', isStaff ? '1' : '2')

        console.log(' Login successful, user type:', isStaff ? 'admin' : 'user')

        // Update auth state immediately
        auth.setAuthState(true, isStaff)
        
        // Force auth reinitialization to ensure proper state
        await auth.reinitialize()
        
        success(`Welcome back, ${username.value}!`)

        // Navigate based on user type with a small delay to ensure state is set
        setTimeout(() => {
          if (isStaff) {
            console.log(' Redirecting to admin panel...')
            router.push('/admin')
          } else {
            console.log(' Redirecting to home...')
            router.push('/')
          }
        }, 100)

      } catch (err) {
        console.error(' Login failed:', err)
        
        if (err.response && err.response.data) {
          error.value = err.response.data.detail || 'Login failed. Please check your credentials.'
        } else {
          error.value = 'Network error. Please try again.'
        }
        
        showError(error.value)
      } finally {
        isLoading.value = false
      }
    }

    return {
      username,
      password,
      showPassword,
      rememberMe,
      isLoading,
      error,
      handleLogin
    }
  }
}
</script>

